const identity = state => state;

const handleActions = (reducers, intitialState) => (
  state = intitialState,
  action = {},
) => (reducers[action.type] || identity)(state, action);

const combineReducers = reducers => (state = {}, action) => {
  const nextState = Object.entries(reducers).reduce(
    (nextState, [key, reducer]) => ({
      [key]: reducer(state[key], action),
      ...nextState,
    }),
    {},
  );

  const hasChanged = Object.keys(reducers).some(
    key => state[key] !== nextState[key],
  );

  return hasChanged ? nextState : state;
};

const queryReducer = handleActions(
  {SET_QUERY: (state, action) => action.payload},
  '',
);

const resultsReducer = handleActions(
  {SET_RESULTS: (state, action) => action.payload},
  [],
);

const selectionReducer = handleActions(
  {
    SET_RESULTS: (state, action) => {
      const max = Math.max(action.payload.length - 1, 0);

      return {
        ...state,
        max,
        current: Math.min(state.current, max),
      };
    },
    PREVIOUS_SELECTION: state => ({
      ...state,
      current: Math.max(state.current - 1, 0),
    }),
    NEXT_SELECTION: state => ({
      ...state,
      current: Math.min(state.current + 1, state.max),
    }),
  },
  {
    current: 0,
    max: 0,
  },
);

const searchReducer = combineReducers({
  query: queryReducer,
  results: resultsReducer,
  selection: selectionReducer,
});

const resetReducer = (state, action = {}) => {
  return action.type === 'RESET' ? undefined : state;
};

export default (state, action) => {
  state = resetReducer(state, action);
  return searchReducer(state, action);
};
