import React, {useEffect, useReducer} from 'react';
import styled from '@emotion/styled';
import {globalHistory as history} from '@reach/router';

import {
  setQuery,
  activateSelection,
  nextSelection,
  previousSelection,
  reset,
} from '../actions';
import reducer from '../reducers';

import Input from './input';
import Results from './results';

function curry(fn, ...args) {
  return (...nArgs) => fn.apply(this, [...args, ...nArgs]);
}

const Form = styled.form`
  margin: 0;
  position: relative;
  width: 100%;
`;

function SearchForm({api}) {
  const [{query, results, selection}, dispatch] = useReducer(
    reducer,
    undefined,
    reducer,
  );

  useEffect(() => history.listen(() => dispatch(reset())), [dispatch]);

  const onSubmit = event => {
    event.preventDefault();
    return false;
  };

  const onKeyUp = event => {
    switch (event.which) {
      case 13:
        event.preventDefault();
        activateSelection(selection, results);
        break;
      case 38:
        event.preventDefault();
        dispatch(previousSelection());
        break;
      case 40:
        event.preventDefault();
        dispatch(nextSelection());
        break;

      default:
      // Do nothing
    }
  };

  return (
    <Form onSubmit={onSubmit} onKeyUp={onKeyUp}>
      <Input value={query} onChange={curry(setQuery, dispatch, api)} />
      <Results results={results} selection={selection.current} />
    </Form>
  );
}

export default SearchForm;
